export default new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '*', 
      component: (resolve)=>require(['./../views/404.vue'],resolve),
    },
    {
      path: '/',
      name: 'Index',
      component: (resolve)=>require(['./../views/index.vue'],resolve),
      meta:{
        title: '首页'
      }
    },
    {
      path: '/blog',
      name: 'Blog',
      component: (resolve)=>require(['./../views/blog/blog.vue'],resolve),
      meta:{
        title: '博客'
      }
    },
    {
      path: '/event',
      name: 'Event',
      component: (resolve)=>require(['./../views/event/events.vue'],resolve),
      meta:{
        title: '比赛'
      }
    },
    {
      path: '/gallery',
      name: 'Gallery',
      component: (resolve)=>require(['./../views/gallery.vue'],resolve),
      meta:{
        title: '照片'
      }
    },
    {
      path: '/about',
      name: 'About',
      component: (resolve)=>require(['./../views/about.vue'],resolve),
      meta:{
        title: '关于'
      }
    },
    {
      path: '/blog/?page=:id',
      name: 'BlogPage',
      component: (resolve)=>require(['./../views/blog/blog.vue'],resolve),
      meta:{
        title: '博客'
      }
    },
    {
      path: '/blog/:id',
      name: 'BlogDetail',
      component: (resolve)=>require(['./../views/blog/detail.vue'],resolve),
    },
    {
      path: '/login',
      name: 'login',
      component: (resolve)=>require(['./../views/login.vue'],resolve),
      meta:{
        title: '登录'
      }
    },
    /*{
      path: '/admin',
      component: AdminBasePage,
      children:[{
        path:'dashboard',
        name:'dashboard',
        component: Dashboard,
        meta:{
          title: '控制台'
        }
      },{
        path:'setting',
        name:'setting',
        component: adminSetting
      },{
        path:'blog',
        name:'blog',
        component: AdminBlog
      },{
        path:'blog/?p=:id',
        name:'blogpaged',
        component: AdminBlog
      },{
        path:'newblog',
        name:'newblog',
        component: NewAdminBlog
      },
      {
        path: 'editblog/:id',
        name: 'EditAdminBlog',
        component: EditAdminBlog
      },{
        path:'event',
        name:'adminevent',
        component: AdminEvent
      },{
        path:'event/?p=:id',
        name:'eventpaged',
        component: AdminEvent
      },{
        path:'newevent',
        name:'newevent',
        component: NewAdminEvent
      },{
        path:'users',
        name:'user',
        component: AdminUser
      },{
        path:'pages',
        name:'pages',
        component: Pages
      }]
    }*/
  ]
})



