let store = new Vuex.Store({
    state:{
        menuIndex: 0
    },
    getters:{
        getMenuIndex(state){
            return state.menuIndex;
        }
    },
    mutations:{
        setMenuIndex(state,index){
            state.menuIndex=index;
        }
    }
})
export default store;


